const tools = require('../../common/tools.js');
export default {
	data() {
		return {
			providerList: [],
			canIUse:wx.canIUse('button.open-type.getUserInfo')
		}
	},
	onLoad() {
		uni.getProvider({
			service: 'oauth',
			success: (result) => {
				this.providerList = result.provider.map((value) => {
					let providerName = '';
					switch (value) {
						case 'weixin':
							providerName = '微信登录'
							break;
						case 'qq':
							providerName = 'QQ登录'
							break;
						case 'sinaweibo':
							providerName = '新浪微博登录'
							break;
						case 'xiaomi':
							providerName = '小米登录'
							break;
						case 'alipay':
							providerName = '支付宝登录'
							break;
						case 'baidu':
							providerName = '百度登录'
							break;
						case 'toutiao':
							providerName = '头条登录'
							break;
					}
					return {
						name: providerName,
						id: value
					}
				});

			},
			fail: (error) => {
				console.log('获取登录通道失败', error);
			}
		});
	},
	methods: {
		bindGetUserInfo (e) {
			if(e.detail.userInfo){		
				let that = this;
				console.log(e.detail.userInfo)
				uni.setStorageSync('nickName', e.detail.userInfo.nickName);
				uni.setStorageSync('avatarUrl', e.detail.userInfo.avatarUrl);
				setTimeout(()=>{
					that.tologin({id:'weixin'});
				},0)
			}	
		},
		tologin(provider) {
			console.log(provider)
			uni.login({
				provider: provider.id,



				success: (res) => {
					console.log('login success:', res);
					console.log(provider)
					uni.getUserInfo({
						provider: provider.id,
						success: function(infoRes) {
							console.log(infoRes);
							uni.setStorageSync('nickName', infoRes.userInfo.nickName);
							uni.setStorageSync('avatarUrl', infoRes.userInfo.avatarUrl);
						},
						fail(error) {
							console.log(error)
						}
					});
					let params = {
						code: res.code
					}
					tools.request("/api/login/authLogin.json", params).then(function(data) {
						uni.setStorageSync('sessionId', data.sessionId);
						tools.toastJump("登录成功","/pages/bindphone/bindphone");
					})

				},
				fail: (err) => {
					console.log('login fail:', err);
				}
			});
		}
	}
}
